/* Make HTML Table Responsive */
.responsive_table {
    margin: auto;
    width: 100%;
    border-collapse: collapse;
    text-align: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; 
    font-size: 0.7rem;
    table-layout: fixed;
}

.responsive_table th {
    display: none;
}

.responsive_table td {
    text-align: center;
    display: block;
    border-bottom: 2px solid;
    border-color: rgb(201, 201, 201);
}

.responsive_table td:before {
    content: attr(data-th) "  ";
    font-weight: bold;
        display: inline-block;
    color: inherit;
}

.responsive_table {
    color: inherit;
    overflow: hidden;
}

.responsive_table tr {
    border-color: inherit;
}

/* For small screen size */
@media screen and (max-width: 768px) {
    /* Heading on small screen */
    .responsive_table td:before {
        width: 100%;
        background-color: black;
        text-align: center!important;
        font-size: 1.2rem;
        color: #fff;
    }
    .responsive_table td:hover {
        background-color: rgb(255, 163, 163);
        color: rgb(255, 0, 0);
    }
    .date-text{
        text-align: center!important;
    }
}

@media screen and (min-width: 767px) {
    .responsive_table td:hover {
        background-color: rgb(255, 163, 163); 
        color: rgb(255, 0, 0);
    }

    .responsive_table td:before {
        display: none;
    }

    .responsive_table th,
    .responsive_table td {
        display: table-cell;
        padding: 0.25em 0.5em;
    }
    .responsive_table th{
        background-color: black;
    text-align: center!important;
    font-size: 1.2rem;
    color: #fff;
    }
    .date-text{
        text-align: right!important;
    }
}

.cell-width {
    max-width: 300px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.todaytd{
    background-color: #AFEEEE!important;
}

body {
    font-family: 'Poppins', sans-serif;
    background: #fafafa;
}

p {
    font-family: 'Poppins', sans-serif;
    font-size: 1.1em;
    font-weight: 300;
    line-height: 1.7em;
    color: #999;
}

a,
a:hover,
a:focus {
    color: inherit;
    text-decoration: none;
    transition: all 0.3s;
}

.navbar {
    padding: 15px 10px;
    background: #fff;
    border: none;
    border-radius: 0;
    margin-bottom: 40px;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
}

.navbar-btn {
    box-shadow: none;
    outline: none !important;
    border: none;
}

.line {
    width: 100%;
    height: 1px;
    border-bottom: 1px dashed #ddd;
    margin: 40px 0;
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */

.wrapper {
    display: flex;
    width: 100%;
    align-items: stretch;
}

#sidebar {
    min-width: 250px;
    max-width: 250px;
    background: #7386D5;
    color: #fff;
    transition: all 0.3s;
}

#sidebar.active {
    margin-left: -250px;
}

#sidebar .sidebar-header {
    padding: 20px;
    background: #6d7fcc;
}

#sidebar ul.components {
    padding: 20px 0;
    border-bottom: 1px solid #47748b;
}

#sidebar ul p {
    color: #fff;
    padding: 10px;
}

#sidebar ul li a {
    padding: 10px;
    font-size: 1.1em;
    display: block;
}

#sidebar ul li a:hover {
    color: #7386D5;
    background: #fff;
}

#sidebar ul li.active>a,
a[aria-expanded="true"] {
    color: #fff;
    background: #6d7fcc;
}

a[data-toggle="collapse"] {
    position: relative;
}

.dropdown-toggle::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
}

ul ul a {
    font-size: 0.9em !important;
    padding-left: 30px !important;
    background: #6d7fcc;
}

ul.CTAs {
    padding: 20px;
}

ul.CTAs a {
    text-align: center;
    font-size: 0.9em !important;
    display: block;
    border-radius: 5px;
    margin-bottom: 5px;
}

a.download {
    background: #fff;
    color: #7386D5;
}

a.article,
a.article:hover {
    background: #6d7fcc !important;
    color: #fff !important;
}

/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */

#content {
    width: 100%;
    padding: 20px;
    min-height: 100vh;
    transition: all 0.3s;
}

/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */

@media (max-width: 768px) {
    #sidebar {
        margin-left: -250px;
    }
    #sidebar.active {
        margin-left: 0;
    }
    #sidebarCollapse span {
        display: none;
    }
}
