.time-card-base {
    background-color: #0e4d92;
    margin: 0 auto;
    width: 300px;
    height: 220px;
    position: relative;
    border-radius: 10px;
  }
  
  h3 {
    color: white;
    text-align: center;
    padding-top: 8px;
    letter-spacing: 1.2px;
    font-weight: 500;
  }
  
  .timefont {
    font-size: 28px;
  }
  
  .stopwatch-card {
    position: relative;
    text-align: center;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    width: 325px;
    height: 190px;
    top: 85px;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  button {
    outline: none;
    background: transparent;
    border: 1px solid blue;
    padding: 5px 10px;
    border-radius: 7px;
    color: blue;
    cursor: pointer;
  }
  
  .buttons {
    display: flex;
    justify-content: space-evenly;
    width: 150px;
    margin: 0 auto;
    margin-top: 5px;
  }
  .timer-center {
    margin: auto;
    width: 50%;
  }
.Auth {
    position: absolute;
    top: 30%;
    text-align: center;
    box-shadow: 0 2px 3px #ccc;
    border: 1px solid #eee;
    padding: 10px;
    box-sizing: border-box;
}
/* for small screen size */
@media screen and (max-width: 900px) {
    .Auth {
        left: 0;
        width: 100%;
    }
}
/* for large screen size */
@media screen and (min-width: 899px) {
    .Auth {
        left: 35%;
        width: 500px;
    }
}
